export default {
	data() {
		return {
			scrollHeight:'',
			lineInfo:{
				lineName:'暂无默认监测点，请选择',
				lineIndex:''
			},
			refreshText:'刷新',
			refreshFlag:0,
			second:60,
			animationDataKVA:{},
			animationDataKVB:{},
			animationDataKVC:{},
			animationDataAA:{},
			animationDataAB:{},
			animationDataAC:{},
			animationDataVoltageA:{},
			animationDataVoltageB:{},
			animationDataVoltageC:{},
			animationDataCurrentA:{},
			animationDataCurrentB:{},
			animationDataCurrentC:{},
			minKVA:0,
			maxKVA:0,
			minKVB:0,
			maxKVB:0,
			minKVC:0,
			maxKVC:0,
			minAA:0,
			maxAA:0,
			minAB:0,
			maxAB:0,
			minAC:0,
			maxAC:0,
			currentAWidth:'60rpx',
			currentBWidth:'80rpx',
			currentCWidth:'100rpx',
			voltageAWidth:'130rpx',
			voltageBWidth:'145rpx',
			voltageCWidth:'160rpx',
			basicInfo:{
				"gdName":'',
				"subName":'',
				"flow":0,
				"flowRatio":0
			},
			constantlyInfo:{},
			showTime:''
		}
	},
	methods: {
		scrollH(){
			var that =this;
			var sys = uni.getSystemInfoSync();
			var winWidth = sys.windowWidth;
			var winrate = 750/winWidth;
			var winHeight= sys.windowHeight;
			var statusBarHeight = sys.statusBarHeight;
			that.scrollHeight = parseInt((winHeight-statusBarHeight-50)*winrate-586)+'rpx';
		},
		chooseLine(){
			var that=this;
			uni.navigateTo({
				url:'../chooseLine/chooseLine'
			});
			uni.$on('lineInfoSelect',function(data){
				that.lineInfo = data;
				that.queryBasicInfo();
				that.queryConstantlyInfo();
			});
			uni.$on('lineInfoQuery',function(data){
				that.lineInfo = data;
				that.queryBasicInfo();
				that.queryConstantlyInfo();
			})
		},
		queryDefaultLine(){
			var that=this;
			if(that.judgeLogin()){
				uni.request({
				    url: that.serverUrl+'/realtimeinfo/getLineId', 
					method:'POST',
				    data: {
				       userIndex:uni.getStorageSync('userInfo').userId
				    },
				    header: {
				        'content-type': 'application/x-www-form-urlencoded'
				    },
					sslVerify:false,
				    success: (res) => {
						if(res.data.resultCode=='10000'){
								var lineInfo = res.data.data
								if(lineInfo){
									that.lineInfo= lineInfo
									that.queryBasicInfo();
									that.queryConstantlyInfo();
								}
								
						}else{
							uni.showModal({
							    title: '提示',
							    content: '查询失败，请重试',
								confirmText:'重试',
							    success: function (res) {
							        if (res.confirm) {
							            that.queryBasicInfo();
							        } else if (res.cancel) {
							            console.log('用户点击取消');
							        }
							    },
							});
						}
				    },
					fail() {
						uni.showModal({
						    title: '提示',
						    content: '网络异常，请检查网络后重试',
							showCancel:false,
						    success: function (res) {
						        if (res.confirm) {
						            console.log('用户点击确定');
						        } else if (res.cancel) {
						            console.log('用户点击取消');
						        }
						    }
						});
					}
				});
			}
		},
		queryBasicInfo(){
			var that=this;
			uni.request({
			    url: that.serverUrl+'/realtimeinfo/getLineBaseInfo', 
				method:'POST',
			    data: {
			       lineIndex:that.lineInfo.lineIndex
			    },
			    header: {
			        'content-type': 'application/x-www-form-urlencoded'
			    },
				sslVerify:false,
			    success: (res) => {
					if(res.data.resultCode=='10000'){
							that.basicInfo = res.data.data
					}else{
						uni.showModal({
						    title: '提示',
						    content: '查询失败，请重试',
							confirmText:'重试',
						    success: function (res) {
						        if (res.confirm) {
						            that.queryBasicInfo();
						        } else if (res.cancel) {
						            console.log('用户点击取消');
						        }
						    },
						});
					}
			    },
				fail() {
					uni.showModal({
					    title: '提示',
					    content: '网络异常，请检查网络后重试',
						showCancel:false,
					    success: function (res) {
					        if (res.confirm) {
					            console.log('用户点击确定');
					        } else if (res.cancel) {
					            console.log('用户点击取消');
					        }
					    }
					});
				}
			});
		},
		queryConstantlyInfo(){
			var that=this;
			uni.request({
			    url: that.serverUrl+'/realtimeinfo/getRealData', 
				method:'POST',
			    data: {
			       lineIndex:that.lineInfo.lineIndex
			    },
			    header: {
			        'content-type': 'application/x-www-form-urlencoded'
			    },
				sslVerify:false,
			    success: (res) => {
					if(res.data.resultCode=='10000'){
						var result= res.data.data;
						that.constantlyInfo = result;
						that.showTime = that.formatTime_second(result.timeId);
						that.maxAA= result.iMax;
						that.maxAB= result.iMax;
						that.maxAC= result.iMax;
						that.maxKVA= result.vMax;
						that.maxKVB= result.vMax;
						that.maxKVC= result.vMax;
						that.createAnimationKVA(parseFloat(result.rmsVA)/parseFloat(result.vMax)*90);
						that.createAnimationKVB(parseFloat(result.rmsVB)/parseFloat(result.vMax)*90);
						that.createAnimationKVC(parseFloat(result.rmsVC)/parseFloat(result.vMax)*90);
						that.createAnimationAA(parseFloat(result.rmsIA)/parseFloat(result.iMax)*90);
						that.createAnimationAB(parseFloat(result.rmsIB)/parseFloat(result.iMax)*90);
						that.createAnimationAC(parseFloat(result.rmsIC)/parseFloat(result.iMax)*90);
						that.drawCurrent(result.baseWaveIA,result.baseWaveIB,result.baseWaveIC,result.baseWavePhaseIA,result.baseWavePhaseIB,result.baseWavePhaseIC)
						that.drawVoltage(result.baseWaveVA,result.baseWaveVB,result.baseWaveVC,result.baseWavePhaseVA,result.baseWavePhaseVB,result.baseWavePhaseVC)
					}else{
						uni.showModal({
						    title: '提示',
						    content: '查询失败，请重试',
							confirmText:'重试',
						    success: function (res) {
						        if (res.confirm) {
						            that.queryConstantlyInfo();
						        } else if (res.cancel) {
						            console.log('用户点击取消');
						        }
						    },
						});
					}
			    },
				fail() {
					uni.showModal({
					    title: '提示',
					    content: '网络异常，请检查网络后重试',
						showCancel:false,
					    success: function (res) {
					        if (res.confirm) {
					            console.log('用户点击确定');
					        } else if (res.cancel) {
					            console.log('用户点击取消');
					        }
					    }
					});
				}
			});
		},
		drawCurrent(currentA,currentB,currentC,angleA,angleB,angleC){
			var that = this;
			if(currentA==currentB&&currentA==currentC){
				that.currentAWidth = '145rpx';
				that.currentBWidth = '145rpx';
				that.currentCWidth = '145rpx';
				that.createAnimationCurrentA(angleA,'11%');
				that.createAnimationCurrentB(angleB,'11%');
				that.createAnimationCurrentC(angleC,'11%');
			}else if(currentA==currentB&&currentA>currentC){
				that.currentAWidth = '145rpx';
				that.currentBWidth = '145rpx';
				that.currentCWidth = '130rpx';
				that.createAnimationCurrentA(angleA,'11%');
				that.createAnimationCurrentB(angleB,'11%');
				that.createAnimationCurrentC(angleC,'12.3%');
			}else if(currentA==currentB&&currentA<currentC){
				that.currentAWidth = '145rpx';
				that.currentBWidth = '145rpx';
				that.currentCWidth = '160rpx';
				that.createAnimationCurrentA(angleA,'11%');
				that.createAnimationCurrentB(angleB,'11%');
				that.createAnimationCurrentC(angleC,'10%');
			}else if(currentA==currentC&&currentA>currentB){
				that.currentAWidth = '145rpx';
				that.currentBWidth = '130rpx';
				that.currentCWidth = '145rpx';
				that.createAnimationCurrentA(angleA,'11%');
				that.createAnimationCurrentB(angleB,'12.3%');
				that.createAnimationCurrentC(angleC,'11%');
			}else if(currentA==currentC&&currentA<currentB){
				that.currentAWidth = '145rpx';
				that.currentBWidth = '160rpx';
				that.currentCWidth = '145rpx';
				that.createAnimationCurrentA(angleA,'11%');
				that.createAnimationCurrentC(angleC,'11%');
				that.createAnimationCurrentB(angleB,'10%');
			}else if(currentB==currentC&&currentA>currentB){
				that.currentAWidth = '160rpx';
				that.currentBWidth = '145rpx';
				that.currentCWidth = '145rpx';
				that.createAnimationCurrentA(angleA,'10%');
				that.createAnimationCurrentB(angleB,'11%');
				that.createAnimationCurrentC(angleC,'11%');
			}else if(currentB==currentC&&currentA<currentB){
				that.currentAWidth = '130rpx';
				that.currentBWidth = '145rpx';
				that.currentCWidth = '145rpx';
				that.createAnimationCurrentA(angleA,'12.3%');
				that.createAnimationCurrentB(angleB,'11%');
				that.createAnimationCurrentC(angleC,'11%');
			}else{
				//排序
				var sortVoltage = [];
				sortVoltage.push({"name":1,"value":currentA});
				sortVoltage.push({"name":2,"value":currentB});
				sortVoltage.push({"name":3,"value":currentC});
				sortVoltage.sort(function(a,b){
					return a.value-b.value;
				})
				for(var i=0;i<sortVoltage.length;i++){
					if(i==0){
						if(sortVoltage[i].name==1){
							that.currentAWidth = '130rpx';
							that.createAnimationCurrentA(angleA,'12.3%');
						}else if(sortVoltage[i].name==2){
							that.currentBWidth = '130rpx';
							that.createAnimationCurrentB(angleB,'12.3%');
						}else{
							that.currentCWidth = '130rpx';
							that.createAnimationCurrentC(angleC,'12.3%');
						}
					}else if(i==1){
						if(sortVoltage[i].name==1){
							that.currentAWidth = '145rpx';
							that.createAnimationCurrentA(angleA,'11%');
						}else if(sortVoltage[i].name==2){
							that.currentBWidth = '145rpx';
							that.createAnimationCurrentB(angleB,'11%');
						}else{
							that.currentCWidth = '145rpx';
							that.createAnimationCurrentC(angleC,'11%');
						}
					}else{
						if(sortVoltage[i].name==1){
							that.currentAWidth = '160rpx';
							that.createAnimationCurrentA(angleA,'10%');
						}else if(sortVoltage[i].name==2){
							that.currentBWidth = '160rpx';
							that.createAnimationCurrentB(angleB,'10%');
						}else{
							that.currentCWidth = '160rpx';
							that.createAnimationCurrentC(angleC,'10%');
						}
					}
				}
			}
		},
